import React, { Component } from 'react';
import { connect } from "react-redux";

import './TargetSelector.css';

import { setTarget } from "./actions";

class TargetSelector extends Component {
  render() {
    return (
      <>
        <button onClick={() => this.props.setTarget('me')} className={`target-selector ${this.props.target === 'me' ? 'target-selector__selected' : ''}`}>Min√§</button>
        <button onClick={() => this.props.setTarget('both')} className={`target-selector ${this.props.target === 'both' ? 'target-selector__selected' : ''}`}>Yhdess√§ ‚ù§Ô∏è</button>
        <button onClick={() => this.props.setTarget('they')} className={`target-selector ${this.props.target === 'they' ? 'target-selector__selected' : ''}`}>H√§n</button>
      </>
    );
  }
}

const mapStateToProps = state => {
  return ({ target: state.target });
};

export default connect(
  mapStateToProps,
  { setTarget }
)(TargetSelector);
